.aside-filter {
  &__tabs {
    display: flex;
    justify-content: space-between;
  }

  &__tab {
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
    letter-spacing: 0.09em;
    text-transform: uppercase;
    color: #C4C4C4;
    width: 50%;
  }

  &__tab.tab--active {
    font-family: "SFProDisplay-Bold", sans-serif;
    color: #2F3035;
    font-weight: 700;
  }

  &__tab.tab--active span{
    display: inline-block;
    border-bottom: 2px solid #1C62CD;
    padding-bottom: 7px;
  }

  &__item-drop {
    margin: 40px 0;
  }

  &__item-title {
    font-family: "SFProDisplay-Semibold", sans-serif;
    font-weight: 600;
    font-size: 16px;
    line-height: 19px;
    padding-left: 26px;
  }

  &__item-title-drop {
    position: relative;
    cursor: pointer;
  }

  &__item-title-drop::before {
    content: '';
    position: absolute;
    background-image: url('../images/arrow-down.svg');
    left: 0;
    bottom: 5px;
    width: 12px;
    height: 8px;
    transform: rotate(0deg);
  }

  &__item-title-drop--active::before {
    transform: rotate(180deg);
  }

  &__content {
    display: flex;
    flex-wrap: wrap;
    margin-top: 10px;
  }

  &__content.aside-filter__content-radio {
    flex-wrap: nowrap;
  }

  &__content-box {
    width: 50%;
    margin-bottom: 10px;
  }

  &__content-label {
    font-size: 14px;
    line-height: 17px;
    cursor: pointer;
  }

  .aside-filter__item-list {
    margin: 46px 0 40px;
  }

  .aside-filter__item-box {
    display: flex;
    justify-content: space-between;
    border-bottom: 2px solid #C4C4C4;
    padding-top: 34px;
  }

  .aside-filter__item-list-title {
    font-family: "SFProDisplay-Semibold", sans-serif;
    font-weight: 600;
    font-size: 16px;
    line-height: 19px;
    padding-bottom: 14px;
    padding-left: 26px;
  }

  .jq-selectbox__select {
    font-family: 'Barlow', sans-serif;
    font-size: 14px;
    line-height: 17px;
    color: #48494D;
    height: 20px;
    padding: 0 25px 0 10px;
    border: none;
    border-radius: 0;
    background: transparent;
    box-shadow: none;
    text-shadow: none;
    outline: none;
  }

  .jq-selectbox li:hover {
    background: rgba(107, 126, 172, 0.05);
    color: rgba(0, 0, 0, 0.6);
  }

  .jq-selectbox li.selected {
    background: rgba(107, 126, 172, 0.05);
    color: rgba(0, 0, 0, 0.6);
  }

  .jq-selectbox li {
    color: rgba(0, 0, 0, 0.6);
  }

  .jq-selectbox__dropdown {
    padding-top: 15px;
    border-radius: 0;
  }

  .jq-selectbox__select:hover {
    background-color: transparent;
  }

  .jq-selectbox.focused .jq-selectbox__select{
    border: none;
  }

  .jq-selectbox__trigger {
    border-left: none;
    background-image: url('../images/arrow-down.svg');
    width: 12px;
    height: 8px;
    top: 6px;
    right: 9px;
  }

  .jq-selectbox__trigger-arrow {
    position: absolute;
    width: 0;
    height: 0;
    border: none;
  }

  .aside-filter__more {
    width: 100%;
  }

  .aside-filter__more-btn {
    font-size: 14px;
    line-height: 17px;
    color: #1C62CD;
    border: none;
    border-bottom: 1px solid #1C62CD;
    background-color: transparent;
    cursor: pointer;
    padding: 0;
  }

  .aside-filter__search {
    width: 100%;
    border: 1px solid #E0E0E0;
    height: 30px;
    padding: 0 20px;
    font-size: 12px;
    line-height: 14px;
    margin-top: 10px;
    margin-bottom: 20px;
  }

  .aside-filter__search::placeholder {
    color: #BDBEC2;
  }

  .aside-filter__item-btn {
    text-align: center;
  }

  .aside-filter__btn-send {
    font-size: 15px;
    line-height: 18px;
    letter-spacing: 0.12em;
    text-transform: uppercase;
    background: #F0F0F4;
    color: #BDBEC2;
    padding: 16px 82px;
    border: none;
    cursor: pointer;
    margin-bottom: 15px;
  }

  .aside-filter__btn-send:hover {
    background-color: #1C62CD;
    color: #ffffff;
  }

  .aside-filter__btn-send--active {
    background-color: #1C62CD;
    color: #ffffff;
  }

  .aside-filter__extra {
    display: inline-block;
    font-size: 13px;
    line-height: 16px;
    color: #2F3035;
    position: relative;
    cursor: pointer;
    padding-right: 29px;
    margin-bottom: 15px;
  }

  .aside-filter__extra::after {
    content: "";
    position: absolute;
    background-image: url('../images/arrow-down.svg');
    width: 12px;
    height: 6px;
    top: 4px;
    right: 0;
  }

  .aside-filter__extra-content {
    display: none;
  }

  .aside-filter__extra.aside-filter__item-title-drop--active::after {
    transform: rotate(180deg);
  }

  .aside-filter__btn-reset {
    font-size: 13px;
    line-height: 16px;
    background-color: transparent;
    padding: 0;
    border: none;
    border-bottom: 1px solid #C4C4C4;
    display: inline-block;
    color: #C4C4C4;
    cursor: pointer;
  }
}