.pagination {
  width: 100%;
  margin-top: 70px;

  &__list {
    display: flex;
    justify-content: center;
  }

  &__item {
    padding: 6px 12px;
    border: 2px solid transparent;
    margin: 0 4px;
    font-size: 18px;
    line-height: 22px;
    color: #C4C4C4;
  }

  &__item.pagination__item--active{
    border: 2px solid #1C62CD;
    color: #1C62CD;
  }

  &__item a{
    display: block;
  }
}